@import url(https://fonts.googleapis.com/css?family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css?family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500&display=swap);
.submit-button {
  background-color: #df629c !important;
  border-color: #df629c !important;
  width: 100%;
}

.search-title,
.search-city {
  color: #5576ce;
  font-family: "Oswald", sans-serif;
  font-size: 30px;
  margin-top: 2px;
}

.search-city {
  margin-top: -4px;
  margin-left: 7px;
}

.form-group {
  margin: 0px !important;
}

.navigation {
  margin-top: 10px;
}

select {
  border: 1px solid #ccc;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: url("data:image/svg+xml,%3Csvg width='1em' height='1em' viewBox='0 0 16 16' class='bi bi-chevron-double-down' fill='white' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' d='M1.646 6.646a.5.5 0 0 1 .708 0L8 12.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3E%3Cpath fill-rule='evenodd' d='M1.646 2.646a.5.5 0 0 1 .708 0L8 8.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3E%3C/svg%3E")
    94% / 8% no-repeat #eee;
  border-radius: 4px;
  font-family: "Oswald", sans-serif !important;
  padding: 2px 0px;
  color: white;
  background-color: #17a2b8;
  text-align: center;
  font-size: 22px !important;
  width: 300px;
}

select:focus {
  outline: none;
}

.category-wrapper {
  width: 175px;
  border: 1px solid gray;
  border-radius: 5px;
}

.category-title {
  font-size: 27px;
  line-height: 1;
  height: 60px;
}

.category-title-wellbeing {
  color: #e8a63c;
}

.category-title-support {
  color: #d42757;
}

.category-title-hospital {
  color: rgb(97, 97, 97);
}

.category-body {
  display: flex;
}

.category-link-wrapper {
  font-size: 33.5px;
  margin-top: -11px;
  padding-left: 5px;
}

.category-list {
  margin-top: -11px;
  color: #d42757;
}

.category-map {
  color: #508ea6;
}

.category-button {
  padding: 0px 5px 0px 5px;
  background-color: transparent;
  border: none;
  width: 80px;
  height: 49px;
}

.category-button:hover,
.category-button:focus {
  border: 1px solid rgb(238, 238, 238);
  border-radius: 3px;
  outline: none;
}

#image {
  background-color: black;
  width: 100px;
  height: 100px;
}

.logo-simples {
  margin: 15px 0 5px 15px !important;
  position: absolute;
}

.description-1,
.description-2 {
  font-size: 40px;
  margin: 0px;
}

.description-wrapper {
  margin-left: 40px;
  margin-bottom: 35px;
  text-shadow: 5px 5px 5px lightgray;
}

.description-1 {
  color: orange;
  margin-right: 10px;
}

.description-2 {
  color: red;
}

.categories-wrapper {
  margin-left: 40px;
  margin-right: 20px;
}

.categories-section-title {
  color: lightgrey;
  font-size: 40px;
  margin-left: 40px;
  margin-right: 40px;
  display: flex;
  align-items: center;
}

.section-title-horizontal-line1 {
  height: 1px;
  width: 50px;
  background-color: lightgray;
  margin-right: 10px;
  opacity: 50%;
}

.section-title-horizontal-line2 {
  height: 1px;
  width: 200px;
  background-color: lightgray;
  margin-left: 10px;
  opacity: 50%;
}

.about-container {
  margin-left: 45px;
  margin-right: 45px;
}

.about-title {
  color: rgb(181, 179, 179);
  text-shadow: 5px 5px 5px lightgray;
}

.about-left-image {
  width: 215px;
  min-width: 215px;
  background-color: gray;
  margin-right: 20px;
}

.request-button {
  border: 1px solid #e8a63c;
  color: #e8a63c;
  outline: none;
  font-weight: 300;
  border-radius: 10px;
  background-color: transparent;
  font-size: 24px;
}

.request-button:focus {
  outline: none;
}

.request-button:hover {
  color: white;
  background-color: #e8a63c;
  outline: none;
}

.about-text-wrapper {
  color: #508ea6;
  font-size: 20px;
  text-align: justify;
  text-justify: auto;
}

.contact-container {
  margin-left: 45px;
  margin-right: 45px;
}

.contact-title {
  color: rgb(181, 179, 179);
  text-shadow: 5px 5px 5px lightgray;
}

.contact-left-image {
  width: 215px;
  min-width: 215px;
  background-color: gray;
  margin-right: 20px;
}

.contact-text-wrapper {
  text-align: justify;
  text-justify: auto;
  font-size: 28px;
}

.contact-hear {
  color: #d42757;
  text-shadow: 5px 5px 5px lightgray;
}

.contact-text {
  font-size: 20px;
  color: #508ea6;
}

.contact-button {
  border: 1px solid #e8a63c;
  color: #e8a63c;
  outline: none;
  font-weight: 300;
  border-radius: 10px;
  background-color: transparent;
  font-size: 24px;
}

.contact-button:focus {
  outline: none;
}

.contact-button:hover {
  color: white;
  background-color: #e8a63c;
  outline: none;
}

.faq-container {
  margin-left: 45px;
  margin-right: 45px;
}

.faq-title {
  color: rgb(181, 179, 179);
  text-shadow: 5px 5px 5px lightgray;
}

.faq-left-image {
  width: 215px;
  min-width: 215px;
  height: 300px;
  background-color: gray;
  margin-right: 20px;
}

.faq-text-wrapper {
  text-align: justify;
  text-justify: auto;
  font-size: 28px;
}

.faq-question {
  color: #d42757;
  text-shadow: 5px 5px 5px lightgray;
  margin-bottom: 0px;
}

.faq-answer {
  color: #508ea6;
  font-size: 20px;
}

#main-menu-wrapper {
  margin-bottom: 40px;
}

.navbar-content {
  margin-top: 2em;
}

.nav-button {
  color: darkgrey !important;
}

.nav-button:hover,
.nav-button:visited,
.nav-button:active {
  color: darkslategray !important;
  text-decoration: none !important;
}

.initial-logo {
  width: 67px;
}

.header-container {
  padding: 20px 45px 0px 45px;
}

.header-divisor {
  margin: 10px 45px;
}

.social-medias {
  width: 32px;
}

#left-side,
#right-side {
  width: 233px;
}

/* Add some App.js styles here that you wanna see applied throughout the app */

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 30px;
  line-height: 60px;
  background-color: lightgray;
}

body {
  margin-left: 7px;
  background-color: #fbfbfb;
  font-family: Quicksand, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

